//
// Box Offer
// --------------------------------------------------

.box-offer {
  display: inline-block;
  border-radius: 4px;
  box-shadow: $shadow-area-sm;

  img {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }

  .box-offer-title {
    font-size: 18px;

    a {
      transition: .3s all ease;

      &:hover {
        color: $primary;
      }
    }
  }

  .box-offer-caption {
    padding: 20px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    background: $white;
  }

  .box-offer-price {
    font-size: 24px;
    line-height: 1;
  }

  * + .list-inline { margin-top: 8px; }

  @include media-breakpoint-up(sm) {
    .box-offer-caption {
      padding: 20px 30px 17px;
    }

    * + .list-inline { margin-top: 0; }
  }

  @include media-breakpoint-up(xl) {
    transition: .3s all ease;

    &:hover {
      box-shadow: $shadow-area-lg;
    }
  }
}


// Box Offer XS
// --------------------------------------------------

.box-offer-xs {

  img {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }

  .box-offer-caption {
    .box-offer-title,
    .box-offer-price,
    .list-inline {
      line-height: 1;
    }

    .box-offer-title,
    .box-offer-price {
      font-size: 16px;
    }
  }

  * + .list-inline { margin-top: 8px; }
  
  @include media-breakpoint-up(sm) {
    .box-offer-caption {
      padding: 15px 21px;
    }

    * + .list-inline { margin-top: 4px; }
  }
  @include media-breakpoint-between(lg, lg) {
    .box-offer-caption {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}


// Box Offer SM
// --------------------------------------------------

.box-offer-sm {
  img {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }

  .box-offer-caption {
    .text-extra-small {
      line-height: 1;
    }

    .box-offer-title,
    .box-offer-price {
      font-size: 20px;
      line-height: 1.3;
    }
  }

  @include media-breakpoint-up(xl) {
    .box-offer-caption {
      .box-offer-title,
      .box-offer-price {
        font-size: 24px;
        line-height: 1.2;
      }
    }
  }
}


// Box Offer Centered
// --------------------------------------------------

.box-offer-centered {
  position: relative;

  .divider {
    margin-top: 0;
    margin-bottom: 0;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: rgba($primary, .9);
    border-radius: 4px;
    transition: .3s all ease;
  }

  &-img-wrap {
    position: relative;

    img, &:before {
      border-radius: 4px;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 4px;
      background: rgba($gray-dark, .3)
    }
  }

  &-caption {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 2;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    transform: translate(-50%, -90%);
    will-change: content;
    transition: .3s all ease;
  }

  &-hover {
    position: relative;
    display: block;

    &-caption {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      padding-left: 20px;
      padding-right: 20px;
      transform: translate(-50%, 30%);
      transition: .3s all ease;
    }
  }

  .divider + h4 { margin-top: 15px; }

  @include media-breakpoint-between(xl, xl) {
    .box-offer-centered-hover-caption {
      h4 {
        font-size: 20px;
      }
    }
  }
  @include media-breakpoint-up(xl) {
    &:before {
      opacity: 0;
    }

    .box-offer-centered-caption {
      transform: translate(-50%, -50%);
    }

    .box-offer-centered-hover-caption {
      transform: translate(-50%, 100%);
      opacity: 0;
      visibility: hidden;
    }

    &:hover {
      &:before {
        opacity: 1;
      }

      .box-offer-centered-caption {
        transform: translate(-50%, -90%);
      }

      .box-offer-centered-hover-caption {
        opacity: 1;
        visibility: visible;
        transform: translate(-50%, 30%);
      }
    }

    .box-offer-centered-title a {
      transition: .3s all ease;

      &:hover {
        color: $black;
      }
    }
  }
}


// Box Offer Variant 2
// --------------------------------------------------

.box-offer-variant-2 {
  .box-offer-img-wrap {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      @include gradient-directional(rgba($black, .5), transparent, 0deg);
    }
  }

  .box-offer-price {
    font-size: 30px;
    position: absolute;
    left: 30px;
    bottom: 25px;
    will-change: content;
  }

  @include media-breakpoint-up(lg) {
    .box-offer-caption {
      padding-left: 23px;
      padding-right: 23px;
      will-change: transform;
    }
  }
  @media (min-width: 1200px) and  (max-width: 1599px) {
    .box-offer-price {
      left: 40px;
    }
  }
  @include media-breakpoint-up(xxl) {
    .box-offer-caption {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}


// Box Offer Variant 3
// --------------------------------------------------

.box-offer-variant-3 {
  .text-small + .text-small { margin-top: 4px; }
  img { margin-top: 8px; }
}

@include media-breakpoint-up(lg) {
  .page .box-offer-variant-3 {
    padding: 20px 10px;
  }

  .box-offer-wrap {
    [class*="col-"] {
      &+ [class*="col-"] {
        .box-offer-variant-3 {
          border-top: 1px solid $brand-gallery;
          border-left: 1px solid $brand-gallery;
        }
      }

      &:nth-child(2), &:nth-child(3) {
        .box-offer-variant-3 {
          border-top: none;
        }
      }

      &:nth-child(4), &:nth-child(7) {
        .box-offer-variant-3 {
          border-left: none;
        }
      }
    }
  }
}

@include media-breakpoint-up(xl) {
  .page .box-offer-variant-3 {
    padding: 27px 20px 22px 53px;
  }
}


// Box Offer Left
// --------------------------------------------------

.box-offer-left {
  img {
    width: 100%;
  }

  .box-offer-caption-inner-wrap {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .box-offer-caption-inner-wrap-bottom {
    display: block;
    align-items: flex-end;
  }
  @include media-breakpoint-up(md) {
    &.box-offer .box-offer-caption {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  @include media-breakpoint-up(xl) {
    .box-offer-description {
      max-width: 330px;
    }

    .box-offer-caption-inner-wrap-bottom {
      display: flex;
    }

    &,.box-offer-caption {
      display: flex;
    }

    &.box-offer .box-offer-caption {
      padding-top: 51px;
      padding-left: 60px;
      padding-right: 52px;
    }

    .box-offer-img-wrap {
      flex-basis: 31.7%;
    }

    .box-offer-title {
      width: 100%;
    }

    .box-offer-caption {
      flex-basis: 68.3%;
      align-items: center;
      flex-wrap: wrap;
      padding: 50px 50px 50px 59px;
    }

    &.box-offer .box-offer-img-wrap img {
      border-top-left-radius: 4px;
      border-top-right-radius: 0;
      border-bottom-left-radius: 4px;
    }

    &.box-offer .box-offer-caption {
      border-bottom-left-radius: 0;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }
  }

  * + .box-offer-title { margin-top: 4px; }
  .box-offer-title + .list-inline { margin-top: 8px; }
  .text-extra-small + .list-inline { margin-top: 4px; }
}

.box-offer-caption-inner-right {
  .list-inline {
    > li {
      vertical-align: middle;
    }
  }
}

.box-offer-caption-inner-wrap-bottom {
  * + .box-offer-caption-inner-right { margin-top: 20px; }

  // Extra large ≥1200px
  @include media-breakpoint-up(xl) {
    * + .box-offer-caption-inner-right { margin-top: 0; }
  }
}

* + .box-offer-caption-inner-wrap { margin-top: 12px; }