//
// Team Member
// --------------------------------------------------

.team-member {
  display: inline-block;

  &-img-wrap {
    display: inline-block;
    border-radius: 50%;
    border: 10px solid $brand-alabaster;

    &-border-darker {
      border-color: $brand-gallery;
    }
  }

  &-description {
    font-size: 10px;
    line-height: 1.2;
  }

  &-scroll-section {
    height: 160px;
    overflow-y: scroll;
  }

  @include media-breakpoint-up(xl) {
    position: relative;
    z-index: 1;

    &:before, .team-member-hover-content {
      transition: .3s all ease;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      background: rgba($white, .7);
    }

    .team-member-hover-content {
      position: absolute;
      top: 50%;
      left: 50%;
      opacity: 0;
      visibility: hidden;
      transform: translate(-50%, 0);
    }

    &:hover {
      &:before {
        opacity: 1;
      }

      .team-member-hover-content {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  * + .team-member-body { margin-top: 20px; }
  * + .team-member-description { margin-top: 8px; }
  * + .team-member-hover-content { margin-top: 20px; }
  * + .team-member-scroll-section { margin-top: 20px; }
  * + .team-member-title { margin-top: 15px; }
  * + p { margin-top: 20px; }

  // Medium ≥768px
  @include media-breakpoint-up(md) {
    * + .team-member-title { margin-top: 0px; }
  }

  // Extra large ≥1200px
  @include media-breakpoint-up(xl) {
    * + .team-member-hover-content { margin-top: 0; }
  }
}

.team-member +  .list-inline { margin-top: 20px; }


// Scroll Bar
// --------------------------------------------------

.team-member {
  ::-webkit-scrollbar-button {
    background-image:url('');
    background-repeat:no-repeat;
    width: 8px;
    height: 0;
  }

  ::-webkit-scrollbar-track {
    background-color: $brand-gallery;
  }

  ::-webkit-scrollbar-thumb {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    background-color: $primary;
  }

  ::-webkit-resizer{
    background-image:url('');
    background-repeat:no-repeat;
    width: 8px;
    height: 0;
  }

  ::-webkit-scrollbar{
    width: 6px;
  }
}
