//
// ToTop
// --------------------------------------------------

.ui-to-top {
  width: 50px;
  height: 50px;
  line-height: 46px;
  text-align: center;
  box-shadow: $shadow-area-md;
  position: fixed;
  border-radius: 50%;
  right: 15px;
  bottom: 15px;
  color: $white;
  background: $primary;
  overflow: hidden;
  z-index: 1;
  transition: .3s all ease;

  transform: translateY(100px);

  &:focus{
    color: $white;
  }

  &:hover {
    color: $white;
    background: $black;
  }

  &.active{
    transform: translateY(0);
  }
}

.mobile .ui-to-top,
.tablet .ui-to-top {
  display: none !important;
}

@include media-breakpoint-up(sm) {
  .ui-to-top {
    right: 40px;
    bottom: 40px;
  }
}


