//
// Boxes
// --------------------------------------------------


// Basic Styling
// --------------------------------------------------

.box-transparent,
.box {
  .unit {
    .text-small + .text-small { margin-top: 4px; }
  }

  svg + h5 { margin-top: 20px; }
  h5 + p { margin-top: 15px; }
  * + .button { margin-top: 30px; }
  * + .section-hidden { margin-top: 4px; }
  * + .section-hidden-2 { margin-top: 15px; }
  p + .rd-mailform { margin-top: 30px; }
  .button + .button { margin-top: 8px; }
  .section-hidden + .button { margin-top: 15px; }
  .section-hidden + .list-inline { margin-top: 15px; }
  .rd-mailform + .text-extra-small { margin-top: 0; }
}

.box, .box-md {
  display: inline-block;
  padding: 20px;
  border-radius: 4px;
  box-shadow: $shadow-area-sm;
  will-change: content;
  transition: .3s all ease;

  @include media-breakpoint-up(xl) {
    padding: 45px 45px 50px;

    &:hover {
      box-shadow: $shadow-area-md;
    }
  }
}


// Sizing
// --------------------------------------------------

.box-xs {
  padding: 34px 30px 23px;

  &.blog-aside {
    padding-bottom: 35px;
  }
  @include media-breakpoint-between(lg, lg) {
    &.blog-aside {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}

.box-sm {
  box-shadow: $shadow-area-sm;

  @include media-breakpoint-up(xl) {
    padding: 25px 10px 20px 31px;
  }
}

.box-lg {
  padding: 45px 25px;
  border-radius: 8px;

  @include media-breakpoint-up(sm) {
    padding: 45px 30px;
  }
  @include media-breakpoint-up(md) {
    padding: 60px 50px;
  }

  &-variant-2 {
    @include media-breakpoint-up(md) {
      padding: 55px 60px;
    }
  }
}


// Box Single Post
// --------------------------------------------------

.box.box-single-post {
  @include media-breakpoint-up(xl) {
    padding: 45px 60px 50px;
  }
}

.box-single-post {
  h4 + * { margin-top: 35px; }
  p + p { margin-top: 20px; }
  * + blockquote { margin-top: 30px; }
  * + .d-inline-block { margin-top: 15px; }
  blockquote + * { margin-top: 30px; }
  
  // Large ≥992px
  @include media-breakpoint-up(lg) {
    * + .quote-simple-2 { margin-top: 50px; }
  }
}

.box-single-post + .hr { margin-top: 60px; }

.box-single-comments {

  h4 + .unit { margin-top: 30px; }
  * + .post-comment-inner { margin-top: 20px; }
}

.hr + .box-single-comments { margin-top: 60px; }

// Box Insets Off
// --------------------------------------------------

.box-insets-off {
  padding: 0;
}


// Box Inner
// --------------------------------------------------

.box-inner {
  padding: 40px 30px;

  @include media-breakpoint-up(lg) {
    padding: 70px 75px 75px 65px;
  }
}


.box-accordion {
  padding: 24px 30px;

  @include media-breakpoint-up(md) {
    padding: 34px 63px;
  }

  p + p { margin-top: 20px; }
  .text-extra-small + p { margin-top: 25px; }
  * + .button { margin-top: 12px; }
}

.box-accordion-xs {

  p + p { margin-top: 0; }

  @include media-breakpoint-up(md) {
    padding: 17px 60px;
  }
}

// Box Search
// --------------------------------------------------

.box-search {
  padding: 0;

  .form-search-submit { color: $gray; }

  .form-search-submit {
    transition: .3s all ease;

    &:hover {
      color: $primary;
    }
  }

  .form-wrap.form-wrap-xs {
    .form-label {
      padding-left: 30px;
    }

    .form-input {
      padding-left: 58px;
      padding-bottom: 13px;
    }

    .form-label, .form-input {
      top: 16px;
    }
  }

  .form-label, .form-input {
    font-size: 18px;
    font-style: italic;
    letter-spacing: .2em;
  }

  .form-input {
    border-radius: 0;
    border-top-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
    border-bottom-width: 2px;
    border-bottom-color: $gray;
    background: $gray-dark;
  }

  .form-search {
    position: relative;
  }

  .form-search-submit {
    position: absolute;
    left: 19px;
    top: 50%;
    z-index: 1;
    margin-top: -5px;
    transform: translateY(-50%);
    background: transparent;
    border: none;
  }

  &-wrap {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    padding: 21px 38px 10px;
    background: $gray-dark;
  }

  &-body {
    padding: 53px 40px;
  }
}

.box-program {
  h3 + p { margin-top: 8px; }

  .unit-body { margin-top: 8px; }

  .bg-gallery + p { margin-top: 20px; }
}

* + .box-program-content { margin-top: 12px; }

.box-careers {
  .icon + p { margin-top: 25px; }
  p + p { margin-top: 8px; }

  // Large ≥992px
  @include media-breakpoint-up(lg) {
    .icon + p { margin-top: 40px; }
  }
}

.box-socials {

  .text-uppercase + p { margin-top: 8px; }
  * + .text-primary { margin-top: 4px; }
}

.box-privacy {
  h4 + p { margin-top: 20px; }
  p + h4 { margin-top: 45px; }
  p + p { margin-top: 20px; }
  * + .box-privacy-bottom { margin-top: 40px; }
  * + .box-terms-bottom { margin-top: 20px; }

  // Large ≥992px
  @include media-breakpoint-up(lg) {
    h4 + p { margin-top: 40px; }
    * + .box-privacy-bottom { margin-top: 110px; }
    * + .box-terms-bottom { margin-top: 40px; }
  }
  
  // Extra large ≥1200px
  @include media-breakpoint-up(xl) {
    * + .box-privacy-bottom { margin-top: 530px; } 
  } 
}