//
// Scaffolding
// --------------------------------------------------


// Reset fonts for relevant elements
input,
button,
select,
textarea {
  outline: none;
}

// Page styles
// --------------------------------------------------

.page {
  position: relative;
  overflow: hidden;
  min-height: 100vh;
}

// Links

a {

  &:hover,
  &:focus {
    outline: none;
    text-decoration: none;
  }
}

// Horizontal rules

hr {
  margin-top: 0;
  margin-bottom: 0;
  border-top: 1px solid $hr-border;
}

img {
  display: inline-block;
  height: auto;
  max-width: 100%;
}

.img-semi-transparent-inverse {
  will-change: opacity;
  transition: .3s opacity ease;

  &:hover {
    opacity: .6;
  }
}

