.lg-sub-html,
.lg-toolbar {
  background-color: rgba(0, 0, 0, .45)
}

@font-face {
  font-family: lg;
  src: url(../fonts/lg.eot?n1z373);
  src: url(../fonts/lg.eot?#iefixn1z373) format("embedded-opentype"), url(../fonts/lg.woff?n1z373) format("woff"), url(../fonts/lg.ttf?n1z373) format("truetype"), url(../fonts/lg.svg?n1z373#lg) format("svg");
  font-weight: 400;
  font-style: normal
}

.lg-icon {
  font-family: 'lg';
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
}

.lg-actions {
  .lg-next,
  .lg-prev {
    width: 44px;
    height: 44px;
    line-height: 42px;
    font-family: 'FontAwesome';
    background-color: rgba(0, 0, 0, 0);
    color: #fff;
    cursor: pointer;
    display: block;
    font-size: 12px;
    margin-top: -10px;
    padding: 0;
    position: absolute;
    top: 50%;
    z-index: 1080;
    border-radius: 50%;
    border: 2px solid #565453;
    outline: 0;
    
    &.disabled {
      pointer-events: none;
      opacity: .5;
    }
    
    &:hover {
      color: #fff;
    }
  }

  .lg-next {
    right: -80px;

    &:before {
      content: "\f054";
      margin-right: -2px;
    }
  }

  .lg-prev {
    left: -80px;

    &:after {
      content: "\f053";
      margin-left: -2px;
    }
  }
}

.lg-outer {
  &.lg-right-end,
  &.lg-left-end {
    .lg-object {
      position: relative;
    }
  }

  &.lg-right-end .lg-object {
    animation: lg-right-end .3s;
  }

  &.lg-left-end .lg-object {
    animation: lg-left-end .3s;
  }
}

.lg-toolbar {
  z-index: 1082;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;

  .lg-icon {
    color: #999;
    cursor: pointer;
    float: right;
    font-size: 24px;
    height: 47px;
    line-height: 27px;
    padding: 10px 0;
    text-align: center;
    width: 50px;
    text-decoration: none !important;
    outline: 0;
    transition: color .2s linear;

    &:hover { color: #fff }
  }

  .lg-close:after { content: "\e070" }
  .lg-download:after { content: "\e0f2" }
}

.lg-sub-html {
  bottom:     0;
  color:      #eeeeee;
  font-size:  16px;
  left:       0;
  padding:    10px 0 40px;
  position:   absolute;
  right:      auto;
  text-align: left;
  z-index:    1080;

  h4 {
    margin:      0;
    font-size:   13px;
    font-weight: 700
  }

  p {
    font-size: 18px;
    margin:    5px 0 0
  }
}

#lg-counter {
  color: #999;
  display: inline-block;
  font-size: 18px;
  padding-left: 20px;
  padding-top: 12px;
  vertical-align: middle
}

.lg-hide-items {
  .lg-prev,
  .lg-next,
  .lg-toolbar {
    opacity: 0;
  }
}

body:not(.lg-from-hash) .lg-outer.lg-start-zoom {
  .lg-object { opacity: 0; }
  .lg-item.lg-complete .lg-object { opacity: 1 }
}


.lg-progress-bar {
  background-color: #333;
  height: 5px;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1083;
  opacity: 0;
  transition: opacity 80ms ease 0s;

  .lg-progress {
    background-color: #a90707;
    height: 5px;
    width: 0
  }
  
  &.lg-start .lg-progress {
    width: 100%
  }

  .lg-show-autoplay & {
    opacity: 1
  }
}

.lg-autoplay-button {
  &:after { content: "\e01d" }
  .lg-show-autoplay &:after { content: "\e01a" }
}


.lg-outer.lg-css3.lg-zoom-dragging .lg-item.lg-complete.lg-zoomable .lg-image,
.lg-outer.lg-css3.lg-zoom-dragging .lg-item.lg-complete.lg-zoomable .lg-img-wrap {
  transition-duration: 0s
}

.lg-outer.lg-use-transition-for-zoom .lg-item.lg-complete.lg-zoomable .lg-img-wrap {
  transition: transform .3s cubic-bezier(0, 0, .25, 1) 0s;
}

.lg-outer.lg-use-left-for-zoom .lg-item.lg-complete.lg-zoomable .lg-img-wrap {
  transition: left .3s cubic-bezier(0, 0, .25, 1) 0s, top .3s cubic-bezier(0, 0, .25, 1) 0s
}

.lg-outer .lg-item.lg-complete.lg-zoomable .lg-img-wrap {
  backface-visibility: hidden
}

.lg-outer .lg-item.lg-complete.lg-zoomable .lg-image {
  transition: transform .3s cubic-bezier(0, 0, .25, 1) 0s, opacity .15s !important;
  transform-origin: 0 0;
  backface-visibility: hidden;
}

#lg-zoom-in:after {
  content: "\e311"
}

#lg-actual-size {
  font-size: 20px
}

#lg-actual-size:after {
  content: "\e033"
}

#lg-zoom-out {
  opacity: .5;
  pointer-events: none
}

#lg-zoom-out:after {
  content: "\e312"
}

.lg-zoomed #lg-zoom-out {
  opacity: 1;
  pointer-events: auto
}

.lg-outer .lg-pager-outer {
  bottom: 60px;
  left: 0;
  position: absolute;
  right: 0;
  text-align: center;
  z-index: 1080;
  height: 10px
}

.lg-outer .lg-pager-outer.lg-pager-hover .lg-pager-cont {
  overflow: visible
}

.lg-outer .lg-pager-cont {
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  position: relative;
  vertical-align: top;
  margin: 0 5px
}

.lg-outer .lg-pager-cont:hover .lg-pager-thumb-cont {
  opacity: 1;
}

.lg-outer .lg-pager-cont.lg-pager-active .lg-pager {
  box-shadow: 0 0 0 2px #fff inset
}

.lg-outer .lg-pager-thumb-cont {
  background-color: #fff;
  color: #fff;
  bottom: 100%;
  height: 83px;
  left: 0;
  margin-bottom: 20px;
  margin-left: -60px;
  opacity: 0;
  padding: 5px;
  position: absolute;
  width: 120px;
  border-radius: 3px;
  transition: opacity .15s ease 0s, transform .15s ease 0s;
}

.lg-outer .lg-pager-thumb-cont img {
  width: 100%;
  height: 100%
}

.lg-outer .lg-pager {
  background-color: rgba(255, 255, 255, .5);
  border-radius: 50%;
  box-shadow: 0 0 0 8px rgba(255, 255, 255, .7) inset;
  display: block;
  height: 12px;
  transition: box-shadow .3s ease 0s;
  width: 12px
}

.lg-outer .lg-pager:focus,
.lg-outer .lg-pager:hover {
  box-shadow: 0 0 0 8px #fff inset
}

.lg-outer .lg-caret {
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px dashed;
  bottom: -10px;
  display: inline-block;
  height: 0;
  left: 50%;
  margin-left: -5px;
  position: absolute;
  vertical-align: middle;
  width: 0
}

.lg-fullscreen:after {
  content: "\e20c"
}

.lg-fullscreen-on .lg-fullscreen:after {
  content: "\e20d"
}

.lg-outer #lg-dropdown-overlay {
  background-color: rgba(0, 0, 0, .25);
  bottom: 0;
  cursor: default;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1081;
  opacity: 0;
  visibility: hidden;
  transition: visibility 0s linear .18s, opacity .18s linear 0s
}

.lg-outer.lg-dropdown-active #lg-dropdown-overlay,
.lg-outer.lg-dropdown-active .lg-dropdown {
  transition-delay: 0s;
  opacity: 1;
  visibility: visible
}

.lg-outer .lg-dropdown {
  background-color: #fff;
  border-radius: 2px;
  font-size: 14px;
  list-style-type: none;
  margin: 0;
  padding: 10px 0;
  position: absolute;
  right: 0;
  text-align: left;
  top: 50px;
  opacity: 0;
  visibility: hidden;
  transition: transform .18s linear 0s, visibility 0s linear .5s, opacity .18s linear 0s
}

.lg-outer .lg-dropdown:after {
  content: "";
  display: block;
  height: 0;
  width: 0;
  position: absolute;
  border: 8px solid transparent;
  border-bottom-color: #fff;
  right: 16px;
  top: -16px
}

.lg-outer .lg-dropdown > li:last-child {
  margin-bottom: 0
}

.lg-outer .lg-dropdown > li:hover .lg-icon,
.lg-outer .lg-dropdown > li:hover a {
  color: #333
}

.lg-outer .lg-dropdown a {
  color: #333;
  display: block;
  white-space: pre;
  padding: 4px 12px;
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 12px
}

.lg-outer .lg-dropdown a:hover {
  background-color: rgba(0, 0, 0, .07)
}

.lg-outer .lg-dropdown .lg-dropdown-text {
  display: inline-block;
  line-height: 1;
  margin-top: -3px;
  vertical-align: middle
}

.lg-outer .lg-dropdown .lg-icon {
  color: #333;
  display: inline-block;
  float: none;
  font-size: 20px;
  height: auto;
  line-height: 1;
  margin-right: 8px;
  padding: 0;
  vertical-align: middle;
  width: auto
}

.lg-outer .lg-inner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%
}

.lg-outer #lg-share {
  position: relative
}

.lg-outer #lg-share:after {
  content: "\e80d"
}

.lg-outer #lg-share-facebook .lg-icon {
  color: #3b5998
}

.lg-outer #lg-share-facebook .lg-icon:after {
  content: "\e901"
}

.lg-outer #lg-share-twitter .lg-icon {
  color: #00aced
}

.lg-outer #lg-share-twitter .lg-icon:after {
  content: "\e904"
}

.lg-outer #lg-share-googleplus .lg-icon {
  color: #dd4b39
}

.lg-outer #lg-share-googleplus .lg-icon:after {
  content: "\e902"
}

.lg-outer #lg-share-pinterest .lg-icon {
  color: #cb2027
}

.lg-outer #lg-share-pinterest .lg-icon:after {
  content: "\e903"
}

.lg-group:after {
  content: "";
  display: table;
  clear: both
}

.lg-outer {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  width: 100%;
  height: 100%;
  text-align: left;
  opacity: 0;
  transition: opacity .15s ease 0s
}

.lg-outer * {
  box-sizing: border-box
}

.lg-outer.lg-visible {
  opacity: 1
}

.lg-outer .lg {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: visible;
  margin-left: auto;
  margin-right: auto;
  max-width: 1170px;
  max-height: 100%
}

.lg-outer .lg-inner {
  position: absolute;
  left: 0;
  top: 0;
  white-space: nowrap
}

.lg-outer {
  .lg-item {
    display:  block;
    position: absolute;
    top:      0;
    left:     0;
    width:    100%;
    height:   100%;
    transition: transform .3s cubic-bezier(0, 0, .25, 1) 0s;
  }

  .lg-img-wrap {
    display:    inline-block;
    text-align: center;
    width:      100%;
    height:     100%;
  }

  .far-prev {
    transform: translateX(-200%);
    z-index: 1;
  }

  .new-prev {
    transform: translateX(-100%);
    z-index: 1;
  }
  
  .new-current {
    transform: translateX(0);
    z-index: 1;
  }
  
  .new-next {
    transform: translateX(100%);
    z-index: 1;
  }
  
  .far-next {
    transform: translateX(200%);
    z-index: 1;
  }
}

.lg-outer .lg-img-wrap:before,
.lg-outer .lg-item:before {
  content: "";
  display: inline-block;
  height: 50%;
  width: 1px;
  margin-right: -1px
}

.lg-outer .lg-img-wrap {
  padding: 0 5px;
}

.lg-outer .lg-item.lg-complete {
  background-image: none
}

.lg-outer .lg-item.lg-current {
  z-index: 1060
}

.lg-outer .lg-image {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  max-height: 100%;
  width: 100% !important;
  height: 680px !important
}

.lg-outer .lg-empty-html,
.lg-outer.lg-hide-download #lg-download {
  display: none
}

.lg-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1040;
  background-color: #000;
  opacity: 0;
  transition: opacity .15s ease 0s
}

.lg-backdrop.in {
  opacity: 1
}

.lg-item {
  backface-visibility: hidden
}


@keyframes lg-right-end {
  0%, 100% {
    left: 0
  }
  50% {
    left: -30px
  }
}

@keyframes lg-left-end {
  0%, 100% {
    left: 0
  }
  50% {
    left: 30px
  }
}
