//
// Responsive unit
// --------------------------------------------------

@include unit-responsive($grid-breakpoints, 30px, 20px);

// Unit Spacing
//

.unit-spacing-xs {
  @include unit-spacing($grid-breakpoints, 15px, 7px);
}

.unit-spacing-sm {
  @include unit-spacing($grid-breakpoints, 22px, 12px);
}

.unit-spacing-md {
  @include unit-spacing($grid-breakpoints, 40px, 20px);
}

.unit-spacing-lg {
  @include unit-spacing($grid-breakpoints, 45px, 25px);
}

.unit-spacing-box-icon {
  @include unit-spacing($grid-breakpoints, 22px, 27px);
}

.unit-wide {
  .unit-body { flex-grow: 1; }
}