//
// Custom form styles
// --------------------------------------------------

.rd-mailform {
  position: relative;
}

// Normalize non-controls
// --------------------------------------------------

.form-input {
  display: block;
  width: 100%;
  height: $form-input-height;
  padding: $padding-base-vertical $padding-base-horizontal;
  font-size: $font-size-base;
  line-height: $line-height-base;
  color: $form-input-color;
  background-color: $form-input-background;
  background-image: none;
  border: $form-input-border;
  border-radius: $input-border-radius;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

label {
  margin-bottom: 10px;
  font-weight: 400;
}

// Common form controls
// --------------------------------------------------

.input-sm,
.input-lg,
.form-input {
  font-size: $form-input-font-size;

  &, &:focus {
    box-shadow: none;
  }
}

textarea.form-input {
  height: $form-textarea-default-height;
  min-height: $form-textarea-default-min-height;
  max-height: $form-textarea-default-max-height;
  resize: vertical;
  line-height: 1.2;
}

.form-wrap.form-wrap-xs textarea.form-input {
  padding-top: 12px;
}

.form-input {
  -webkit-appearance: none;
  border-radius: 30px;
  line-height: $form-input-height - ($padding-base-vertical + 1px)*2;

  &:focus {
    outline: 0;
  }
}

// Form groups
// -------------------------

.form-wrap {
  position: relative;

  &.form-wrap-xs {
    margin-bottom: 10px;

    .form-label {
      top: 20px;
      left: 28px;
    }
    .form-input {
      height: 40px;
      padding-top: 6px;
      padding-bottom: 6px;
      padding-left: 25px;
    }
  }

  &.form-wrap-xxs {
    margin-bottom: 10px;

    .form-label, .form-input {
      font-size: 12px;
    }

    .form-label {
      top: 15px;
      left: 15px;
    }

    .form-input {
      height: 30px;
      padding-top: 3px;
      padding-bottom: 3px;
      padding-left: 13px;
    }
  }

  @include media-breakpoint-down(sm) {
    margin-bottom: 5px;
  }
}

.form-wrap + .form-button { margin-top: 12px; }
.form-wrap + .form-button-2 { margin-top: 20px; }


.form-wrap.form-offset-bottom-none {
  margin-bottom: 0;
}

// Form labels
// -------------------------

.form-label {
  position: absolute;
  top: 25px;
  left: $padding-base-horizontal;
  font-size: $form-input-font-size;
  color: $form-input-color-placeholder;
  pointer-events: none;
  z-index: 1;
  transition: .3s;
  transform: translateY(-50%);

  &.focus {
    opacity: 0;
  }

  &.auto-fill {
    color: $form-input-color;
  }
}

.form-label-outside {
  @include media-breakpoint-up(md) {
    position: static;
    &, &.focus, &.auto-fill {
      transform: none;
      color: $form-input-color-placeholder;
      font-size: $form-input-font-size;
    }
  }
}

// Form validation
// -------------------------
.form-validation {
  position: absolute;
  right: 22px;
  top: 2px;
  font-weight: 400;
  font-size: 10px;
  line-height: 11px;
  color: $brand-danger;
  margin-top: 2px;
  transition: .3s;
  z-index: 1;
}

// Error Styling
//
.has-error {
  @include form-input-validation-custom($state-danger-text, $state-danger-bg, $state-danger-bg);
}

// Success Styling
//
.has-success {
  @include form-input-validation-custom($state-success-text, $state-success-bg, $state-success-bg);
}

// Warning Styling
//
.has-warning {
  @include form-input-validation-custom($state-warning-text, $state-warning-bg, $state-warning-bg);
}

// Info Styling
//
.has-info {
  @include form-input-validation-custom($state-info-text, $state-info-bg, $state-info-bg);
}


// Form output
// -------------------------

#form-output-global {
  position: fixed;
  bottom: 30px;
  left: 15px;
  visibility: hidden;
  transform: translateX(-500px);
  transition: .3s all ease;
  z-index: 9999999;

  &.active {
    transform: translateX(0);
    visibility: visible;
  }

  @include media-breakpoint-up(sm) {
    left: 30px;
  }
}

.form-output {
  position: absolute;
  top: 100%;
  left: 0;
  font-size: 14px;
  line-height: 1.5;
  margin-top: 2px;
  transition: .3s;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
  }
  &.error {
    color: $brand-danger;
  }
  &.success {
    color: $brand-success;
  }
}

// Radio and Checkbox Custom
// -------------------------

// Base Styles
//
.radio .radio-custom,
.radio-inline .radio-custom,
.checkbox .checkbox-custom,
.checkbox-inline .checkbox-custom {
  opacity: 0;

  &, &-dummy {
    position: absolute;
    width: 12px;
    height: 12px;
    margin-left: -20px;
    margin-top: 5px;
    outline: none;
    cursor: pointer;
  }

  &-dummy {
    pointer-events: none;
    background: transparent;
    box-shadow: none;
    border: 1px solid $black;

    &:after {
      position: absolute;
      opacity: 0;
    }
  }

  &:focus {
    outline: none;
  }
}

.radio-custom:checked + .radio-custom-dummy:after,
.checkbox-custom:checked + .checkbox-custom-dummy:after {
  opacity: 1;
}

// Custom Radio Styles
//
.radio,
.radio-inline {
  .radio-custom-dummy {
    border-radius: 50%;

    &:after {
      content: '';
      top: 3px;
      right: 3px;
      bottom: 3px;
      left: 3px;
      background: $gray-darker;
      border-radius: 50%;
    }
  }
}

.radio-inline, .checkbox-inline {
  position: relative;
  display: inline-block;
  padding-left: 20px;
  margin-bottom: 0;
  vertical-align: middle;
  font-weight: normal;
  cursor: pointer;
}

// Custom Checkbox Styles
//
.checkbox,
.checkbox-inline {
  padding-left: 20px;
  .checkbox-custom-dummy {
    pointer-events: none;
    border-radius: 1px;
    margin-left: 0;
    left: 0;

    &:after {
      content: '\f00c';
      font-family: 'FontAwesome';
      font-size: 10px;
      line-height: 10px;
      position: absolute;
      top: 0;
      left: 0;
      color: $primary;
    }
  }
}

.page {
  .checkbox-inline-right {
    margin-top: -15px;
    padding-right: 20px;

    .checkbox-custom, .checkbox-custom-dummy {
      transform: translate(103px, 4px);
    }

    @include media-breakpoint-up(sm) {
      margin-top: 0;
      padding-right: 10px;
    }
  }

  .checkbox-inline-left {
    &, span {
      line-height: 1;
    }

    .checkbox-custom-dummy {
      margin-top: 3px;
    }

    > span {
      padding-left: 7px;
    }
  }
}


// Form Inline Search
//
.form-inline-search {
  padding-left: 20px;
  padding-right: 20px;

  .button-wrap { margin-top: 15px; }

  // Medium ≥576px
  @include media-breakpoint-up(sm) {
    .button-wrap { margin-top: 0; }
  }
  
  // Medium ≥768px
  @include media-breakpoint-up(md) {
    padding-left: 0;
    padding-right: 0;
  }

  @include media-breakpoint-up(md) {
    font-size: 0;

    .form-wrap-xs {
      .form-input {
        border-radius: 0;
      }
    }

    .form-inline-item {
      display: inline-block;
      margin: 0;

      &:first-child {
        position: relative;

        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: 1px;
          height: 20px;
          width: 1px;
          background: $gray-lighter;
          transform: translateY(-50%);
        }

        .form-input {
          border-top-left-radius: 30px;
          border-bottom-left-radius: 30px;
        }
      }

      &:nth-child(3) {
        position: relative;

        &:after {
          content: '→';
          position: absolute;
          top: 50%;
          left: -10px;
          margin-top: -3px;
          font-size: 20px;
          transform: translateY(-50%);
        }
      }

      &:nth-last-child(2) {
        position: relative;

        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 4px;
          height: 20px;
          width: 1px;
          background: $gray-lighter;
          transform: translateY(-50%);
        }
      }

      &:last-child {
        vertical-align: top;

        .button {
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
        }
      }

      .button {
        height: 40px;

        @include media-breakpoint-down(md) {
          min-width: 100px;
        }
      }

      .form-input {
        border-left: none;
        border-right: none;
      }
    }

    .form-inline-item-xs {
      max-width: 100px;
    }
  }
  @include media-breakpoint-up(lg) {
    .form-inline-item-xs {
      max-width: 115px;
    }
  }
}

.ie-11 .form-inline-search {
  .form-wrap:first-child {
    margin-right: -1px;
  }
}


// Form Blog Search
//

.form-blog-search {
  position: relative;
  
  .form-search-submit {
    position: absolute;
    top: -1px;
    left: 14px;
    bottom: 0;
    z-index: 1;
    font-size: 16px;
    color: $brand-silver-chalice;
    background: transparent;
    border: none;
    transition: .3s all ease;

    &:hover {
      color: $primary;
    }
  }


  .form-wrap {
    .form-label {
      top: 20px;
      left: 47px;
    }

    .form-input {
      padding-left: 46px;
      background: $brand-wild-wand;
      border-color: $brand-wild-wand;
    }
  }

  @include media-breakpoint-up(lg) {
    .form-search-submit {
      left: 10px;
    }

    .form-wrap {
      .form-label {
        left: 40px;
      }
    }
  }
  @include media-breakpoint-up(xl) {
    .form-search-submit {
      left: 14px;
    }

    .form-wrap {
      .form-label {
        left: 47px;
      }
    }
  }
}

.tablet, .mobile {
  .form-blog-search {
    .form-search-submit {
      left: 2px;
    }
  }
}

.tablet, .mobile {
  .form-blog-search.form-blog-search-type-2 {
    .form-search-submit {
      left: 7.5px;
    }
  }
}


// Form Comment
//

.form-comment {
  position: relative;

  .form-label, .form-input {
    color: $brand-silver-chalice;
  }

  .form-wrap.form-wrap-xs .form-label {
    top: 19px;
    left: 22px;
  }

  .form-input {
    border-radius: 3px;
    border-color: $brand-alto;
  }

  button {
    position: absolute;
    top: 50%;
    right: 15px;
    background: transparent;
    border: none;
    transform: translateY(-50%);
  }

  &.form-comment-comment-inner {
    button {
      right: 8px;
    }
  }
}

.tablet, .mobile {
  .form-comment button {
    right: 2px;
  }
}


// Form Search Results
//

.form-search-results {
  position: relative;

  .form-wrap.form-wrap-xs .form-input {
    padding-right: 90px;
  }

  .form-search-submit {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    padding: 0 30px;
    font-size: 16px;
    color: $white;
    background: $primary;
    border: none;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    transition: .3s all ease;

    &:hover {
      background: darken($primary, 10%);
    }
  }
}