//
// Dividers
// --------------------------------------------------


// Hr
// --------------------------------------------------

.hr {
  height: $hr-height;
  padding: $hr-padding;
  margin-top: 0;
  margin-bottom: 0;
  background: $gray-lighter;
  border: none;
}

.hr-dashed{
  height: $hr-dashed-height;
  border-bottom: $hr-dashed-border-bottom;
  background-color: $hr-dashed-background;
}


// Text Subline
// --------------------------------------------------

.text-subline {
  position: relative;
  height: $text-subline-height;
  padding: $text-subline-padding;
  background: $text-subline-background;
  border: none;

  &:before {
    content: '';
    position: absolute;
    top: $text-subline-before-position-top;
    left: $text-subline-before-position-left;
    height: $text-subline-before-height;
    width: $text-subline-before-width;
    background: $text-subline-before-background;
  }
}


// Divider
// --------------------------------------------------

// Base Styles

.divider{
  width: $divider-width;
  height: $divider-height;
  padding: $divider-padding;
  margin: $divider-offsets;
  background: $divider-background;
  border: none;
}

.divider-vertical {
  width: $divider-vertical-width;
  height: $divider-vertical-height;
  background: $divider-vertical-background;
}


// Divider Both Lines
// --------------------------------------------------

.divider-both-lines {
  position: relative;
  display: inline-block;
  padding-right: 20px;
  padding-left: 20px;

  &:before, &:after {
    content: '';
    position: absolute;
    top: 50%;
    width: 100%;
    height: 1px;
    margin-top: 2.5px;
    background: $brand-gallery;
    transform: translateY(-50%);
  }

  &:before {
    right: 100%;
  }

  &:after {
    left: 100%;
  }
}


// Sizing

.divider-xs {
  width: $divider-xs-width;
  height: $divider-xs-height;
}

.divider-sm {
  width: $divider-sm-width;
}

.divider-lg {
  width: $divider-lg-width;
  height: $divider-lg-height;
}

// Styles

@include indent-responsive(hr, null, (margin-left, margin-right), $grid-breakpoints, auto);
@include indent-responsive(hr, left, margin-left, $grid-breakpoints, 0);
@include indent-responsive(hr, right, margin-right, $grid-breakpoints, 0);