// Variables
//
// Copy settings from this file into the provided `_custom.scss` to override
// the Bootstrap defaults without modifying key, versioned files.
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.
//
// Temporary, used for calculation variables should by named `$_variable-name`
//
// Table of Contents
//
// Color system
// Body
// Page
// Components
// Fonts
// Links
// Z-index master list
// Grid breakpoints
// Grid containers
// Grid columns
// Buttons
// Form
// Pagination 
// Tooltips


//
// Color system
//

// Gray colors
$white:  #fff;
$gray-100: #edeff4;
$gray-200: #d7d7d7;
$gray-300: #ccc;
$gray-400: #b7b7b7;
$gray-500: #9b9b9b;
$gray-600: #868e96;
$gray-700: #2c343b; 
$gray-800: #151515;
$gray-900: #111;
$black:  #000;


$gray-darker:             #222;
$gray-dark:               #333;
$gray:                    #888;
$gray-light:              #777;
$gray-lighter:            #ddd;

$primary:                 #ff9f1c;
$brand-success:           #5cb85c;
$brand-info:              #5bc0de;
$brand-warning:           #f0ad4e;
$brand-danger:            #d9534f;

// Derivatives colors

$brand-gallery:           #eee;
$brand-wild-wand:         #f5f5f5;
$brand-silver-chalice:    #aaa;
$brand-turquoise:         #2ec4b6;
$brand-alabaster:         #f8f8f8;
$brand-silver:            #ccc;
$brand-mercury:           #e5e5e5;
$brand-alto:              #ddd;
$brand-alabaster:         #f9f9f9;


$theme-colors: ();

$white-light: rgba($white, .3);

//
// Body 
//
$body-bg:       $white;
$body-color:    $gray;

$content-bg:            $white;
$header-bg:             $white;
$footer-bg: 			$white;

// Accent colors
$danger-color:    #f5543f;
$success-color:   #98bf44;

// Theme colors 
$black-invariable:      #000;
$white-invariable:      #fff;

// Additional colors
$facebook:                #4d70a8;
$facebook-light:          #748abc;
$facebook-darken:         darken($facebook, 5%);

$twitter:                 #02bcf3;
$twitter-light:           #81cbdb;
$twitter-darken:          darken($twitter, 5%);
 
$google:                  #e2411e;
$google-light:            #de7272;
$google-darken:           darken($google, 5%);


//
// Page
//
$page-boxed-width:               1600px;
$page-boxed-background-color:    $gray-100;
$page-boxed-background-image:    url(../images/bg-pattern-boxed.png);


//
// Components
//
// Define common padding and border radius sizes and more.
$border-width: 1px;

$border-radius:          0;
$border-radius-lg:       .3rem;
$border-radius-sm:       .2rem;

$transition-base:        all .3s ease-in-out;

// Map
//
$map-height: 200px;
$map-xs-height: 250px;
$map-md-height: 450px;


// Custom
$shadow-area-1:   -3px 0px 50px -2px rgba(0, 0, 0, 0.09);
$shadow-area-2:   1px 1px 10px 0 rgba(0, 0, 0, 0.2);


//
// Fonts
//
// Font, line-height, and color for body text, headings, and more.
$font-family-sans-serif:   "Lato", Helvetica, Arial, sans-serif;
$font-family-sans-serif-1: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
$font-family-serif:        Georgia, "Times New Roman", Times, serif;
$font-family-monospace:    Menlo, Monaco, Consolas, "Courier New", monospace;

$font-family-base:       $font-family-sans-serif;
$font-family-sec:        $font-family-serif;

// Base fonts
$font-size-base: 16px;
$font-size-lg:   18px;
$font-size-sm:   12px;

$_lh-base: 24px;
$_lh-lg:   28px;
$_lh-sm:   18px;

$font-weight-base: 400;
$font-weight-bold: 700;

$layout-boxed-width:      1920px;

// ** < Calculated font values >
$line-height-base: ($_lh-base / $font-size-base);
$line-height-lg:   ($_lh-lg / $font-size-lg);
$line-height-sm:   ($_lh-sm / $font-size-sm);
// ** </ Calculated font values >


// Headings
$headings-font-family:   inherit;
$headings-font-weight:   400;
$headings-line-height:   1.1;
$headings-color:         $black;

// Template Heading Values
$_h1-fsz: 60;
$_h2-fsz: 48;
$_h3-fsz: 36;
$_h4-fsz: 30;
$_h5-fsz: 24;
$_h6-fsz: 22;

$_h1-lh: 72;
$_h2-lh: 60;
$_h3-lh: 50;
$_h4-lh: 37;
$_h5-lh: 32;
$_h6-lh: 28;

// ** < Calculated Headings values >
$h1-font-size: $_h1-fsz * 1px;
$h2-font-size: $_h2-fsz * 1px;
$h3-font-size: $_h3-fsz * 1px;
$h4-font-size: $_h4-fsz * 1px;
$h5-font-size: $_h5-fsz * 1px;
$h6-font-size: $_h6-fsz * 1px;

$h1-line-height: ($_h1-lh / $_h1-fsz); 
$h2-line-height: ($_h2-lh / $_h2-fsz);
$h3-line-height: ($_h3-lh / $_h3-fsz);
$h4-line-height: ($_h4-lh / $_h4-fsz);
$h5-line-height: ($_h5-lh / $_h5-fsz);
$h6-line-height: ($_h6-lh / $_h6-fsz);
// ** </ Calculated Headings values >


// Icon Fonts
$fa:  'FontAwesome';
$mdi: 'Material Design Icons';
$mi:  'Material Icons';

//== Iconography
//
//## Specify custom location and filename of the included Glyphicons icon font. Useful for those including Bootstrap via Bower.

//** Load fonts from this directory.
$icon-font-path: "../fonts/";

//** File name for all font files.
$icon-font-awesome:        "fontawesome-webfont";
$icon-material-design:     "materialdesignicons-webfont";

//** Element ID within SVG icon file.
$icon-font-awesome-id:     "fontawesomeregular";
$icon-material-design-id:  "materialdesigniconsregular";

//== Components
//
//## Define common padding and border radius sizes and more. Values based on 14px text and 1.428 line-height (~20px to start).

$padding-base-vertical:     9px;
$padding-base-horizontal:   20px;

$padding-large-vertical:    $padding-base-vertical*1.2;
$padding-large-horizontal:  $padding-base-horizontal*1.2;

$padding-small-vertical:    $padding-base-vertical*0.8;
$padding-small-horizontal:  $padding-base-horizontal*0.8;

$padding-xs-vertical:       $padding-base-vertical*0.4;
$padding-xs-horizontal:     $padding-base-horizontal*0.4;

$line-height-large:         1.3333333; // extra decimals for Win 8.1 Chrome
$line-height-small:         1.5;

$border-radius:             0;
$border-radius-large:       6px;
$border-radius-small:       3px;

//** Width of the `border` for generating carets that indicator dropdowns.
$caret-width-base:          4px;
//** Carets increase slightly in size for larger components.
$caret-width-large:         5px;

//== Buttons
//
//## For each of Bootstrap's buttons, define text, background and border color.

$btn-lh-size: 24;
$btn-default-bg:                 $black;
$btn-default-border:             $black;

// Lead
$lead-font-size:   24px;
$lead-line-height: 34px;
$lead-font-weight: 300;

// Hr
$hr-border-color: $gray-200;
$hr-border-width: $border-width;

// Mark
$mark-padding: 5px 10px;
$mark-bg: $primary;

$dt-font-weight: inherit;
$list-inline-padding: 5px;

// Code
$code-font-size:              90%;
$code-padding-y:              10px;
$code-padding-x:              5px;
$code-color:                  $gray-900;
$code-bg:                     $gray-100;


//
// Links
//
$link-color:            $primary;
$link-decoration:       none;
$link-hover-color:      darken($link-color, 15%); 
$link-press-color:      darken($link-color, 15%);
$link-hover-decoration: underline;


// Z-index master list
//
// Warning: Avoid customizing these values. They're used for a bird's eye view
// of components dependent on the z-axis and are designed to all work together.

$zindex-dropdown:           1000;
$zindex-sticky:             1020;
$zindex-fixed:              1030;
$zindex-modal-backdrop:     1040;
$zindex-modal:              1050;
$zindex-popover:            1060;
$zindex-tooltip:            1070;
// Custom
$zindex-rd-navbar:          1080;
$zindex-layout-panel:       1100;


//
// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.
$grid-breakpoints: (
  xs:   0,
  sm:   576px,
  md:   768px,
  lg:   992px,
  xl:   1200px,
  xxl:  1800px
);
 
@include _assert-ascending($grid-breakpoints, "$grid-breakpoints");
@include _assert-starts-at-zero($grid-breakpoints);


$screen-fullhd: 1600px;


// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
    sm:   540px,
    md:   720px,
    lg:   960px,
    xl:   1200px
);
@include _assert-ascending($container-max-widths, "$container-max-widths");


//
// Grid columns
//
// Set the number of columns and specify the width of the gutters.
$grid-columns: 12;
$grid-gutter-width: 30px;
$grid-gutter-width-narrow: 30px;


//
// Buttons
//

$input-btn-padding-y:       11px;
$input-btn-padding-x:       35px;
$input-btn-font-size:       14px;
$input-btn-line-height:     1.25;

$input-btn-padding-y-sm:    5px;
$input-btn-padding-x-sm:    25px;
$input-btn-line-height-sm:  1.5;

$input-btn-padding-y-lg:    12px;
$input-btn-padding-x-lg:    50px;
$input-btn-line-height-lg:  1.5;


$btn-font-family:                $font-family-sec;
$btn-font-weight:                400; 
$btn-letter-spacing:             .1em;
$btn-text-transform:             uppercase;

$btn-box-shadow:                 0;
$btn-focus-box-shadow:           0;
$btn-active-box-shadow:          0;

$btn-link-disabled-color:        $gray-600;

$btn-block-spacing-y:            .5rem;

// Allows for customizing button radius independently from global border radius
$btn-border-radius:              $border-radius;
$btn-border-radius-lg:           $border-radius-lg;
$btn-border-radius-sm:           $border-radius-sm; 

$btn-transition:                 all .15s ease-in-out;

//
// Forms
//
$form-input-height:             50px;
$form-input-lg-height:          60px;
$form-input-sm-height:          40px;

$form-input-color:              $gray;
$form-input-color-placeholder:  $gray;

$form-input-background:         $white;
$form-input-border-color:       $brand-silver;
$form-input-border-radius:      $border-radius;
$form-input-background-disabled:       $gray-lighter;

$form-input-font-size:          14px;
$form-input-line-height:        24px;
$form-input-font-weight:        400;

$form-input-border:             1px solid $form-input-border-color;
$form-input-padding-horizontal: 19px;
$form-input-line-height:        round($form-input-font-size * 1.7);

$form-input-padding-vertical:   round(($form-input-height - $form-input-line-height) / 2) + 1px;
$form-input-padding:            $form-input-padding-vertical $form-input-padding-horizontal;

@if ($form-input-border != none) {
  $form-input-padding-vertical: round(($form-input-height - $form-input-line-height - (nth($form-input-border, 1) * 2)) / 2);
  $form-input-padding: $form-input-padding-vertical $form-input-padding-horizontal;
}

$form-textarea-default-height:       170px;
$form-textarea-default-min-height:   $form-input-height;
$form-textarea-default-max-height:   370px;


$form-feedback-focus-color:   $primary;
$form-feedback-valid-color:   $success-color;
$form-feedback-invalid-color: $danger-color;

//** Default `.form-input` border radius
// This has no effect on `<select>`s in some browsers, due to the limited stylability of `<select>`s in CSS.
$form-input-border-color-radius:            $border-radius;

//** Border color for inputs on focus
$form-input-border-color-focus:             $primary;

//** Large `.form-input` height
$input-height-large:             (ceil($font-size-lg * $line-height-large) + ($padding-large-vertical * 2) + 2);
//** Small `.form-input` height
$input-height-small:             (floor($font-size-sm * $line-height-small) + ($padding-small-vertical * 2) + 2);

//** Disabled cursor for form controls and buttons.
$cursor-disabled:                not-allowed;

//
// Pagination
//
$pagination-padding-y:                 10px;
$pagination-padding-x:                 10px;
$pagination-font-size:                 14px;
$pagination-line-height:               24px;

$pagination-color:                     $primary;
$pagination-bg:                        $white;
$pagination-border-width:              2px;
$pagination-border-color:              $gray-200;
$pagination-border:                    $gray-lighter;

$pagination-hover-color:               $link-hover-color;
$pagination-hover-bg:                  $pagination-hover-bg;
$pagination-hover-border-color:        $primary;
$pagination-hover-border:              $gray-lighter;

$pagination-active-color:              $white;
$pagination-active-bg:                 $primary;
$pagination-active-border-color:       $primary;
$pagination-active-border:             $primary;

$pagination-disabled-color:            $gray-light;
$pagination-disabled-bg:               $white;
$pagination-disabled-border-color:     $gray-200;
$pagination-disabled-border:           $gray-lighter;

//== Form states and alerts
//
//## Define colors for form feedback states and, by default, alerts.

$state-success-text:             #3c763d;
$state-success-bg:               #dff0d8;
$state-success-border:           darken(adjust-hue($state-success-bg, -10), 5%);

$state-info-text:                #31708f;
$state-info-bg:                  #d9edf7;
$state-info-border:              darken(adjust-hue($state-info-bg, -10), 7%);

$state-warning-text:             #8a6d3b;
$state-warning-bg:               #fcf8e3;
$state-warning-border:           darken(adjust-hue($state-warning-bg, -10), 5%);

$state-danger-text:              #a94442;
$state-danger-bg:                #f2dede;
$state-danger-border:            darken(adjust-hue($state-danger-bg, -10), 5%);

//
// Tooltips
//
$tooltip-max-width:           200px;
$tooltip-color:               $white;
$tooltip-bg:                  $black;
$tooltip-opacity:             .9;
$tooltip-padding-y:           6px;
$tooltip-padding-x:           10px;
$tooltip-margin:              0;

$tooltip-arrow-width:         5px;
$tooltip-arrow-height:        6px;
$tooltip-arrow-color:         $tooltip-bg;


//== Shadows
//
//##

$shadow-area-ambient: 0px 0px 1px 0 rgba(0, 0, 0, 0.1);
$shadow-area-xxs: 1px 1px 1px rgba(0, 0, 0, 0.1);
$shadow-area-xs: 0 1px 4px -1px rgba(0, 0, 0, 0.1);
$shadow-area-sm: 0 1px 10px 0 rgba(0, 0, 0, 0.1);
$shadow-area-md: 0 3px 11px 0 rgba(0, 0, 0, 0.1);
$shadow-area-lg: 0 5px 23px 0 rgba(0, 0, 0, 0.1);
$shadow-area-xl: 0 12px 36px 0 rgba(0, 0, 0,.1);


//== Dividers
//
//##

//** Hr
$hr-height:                           1px;
$hr-padding:                          0px;
$hr-offset-top:                       30px;
$hr-offset-bottom:                    30px;
$hr-border:                           $gray-lighter;
//** hr dashed
$hr-dashed-background:                transparent;
$hr-dashed-height:                    0;
$hr-dashed-border-bottom:             1px dashed $gray;
//** Divider
$divider-background:                  $gray;
$divider-width:                       50px;
$divider-height:                      4px;
$divider-padding:                     0;
$divider-offsets:                     29px auto 34px;
//** Divider Vertical
$divider-vertical-background:         $gray;
$divider-vertical-width:              1px;
$divider-vertical-height:             24px;
//** Sizing
$divider-xs-width:                    30px;
$divider-xs-height:                   2px;
$divider-sm-width:                    30px;
$divider-lg-width:                    70px;
$divider-lg-height:                   4px;
//** Text Subline
$text-subline-background:             $primary;
$text-subline-padding:                0;
$text-subline-height:                 1px;
//** before
$text-subline-before-background:      $primary;
$text-subline-before-position-top:    0;
$text-subline-before-position-left:   0;
$text-subline-before-width:           50px;
$text-subline-before-height:          1px;


//== RD Navbar
//
//##

$rd-navbar-min-font-size: 24px;
$rd-navbar-min-line-height: 48px;
$rd-navbar-min-height: 56px;

$rd-navbar-color: $gray-dark;
$rd-navbar-background: $white;
$rd-navbar-shadow: none;
$rd-navbar-width: 1200px;

//RD Navbar panel
$rd-navbar-panel-color: $rd-navbar-color;
$rd-navbar-panel-background: $rd-navbar-background;

//RD Navbar nav
$rd-navbar-nav-min-width: 270px;
$rd-navbar-nav-color: $rd-navbar-color;
$rd-navbar-nav-background: transparent;
$rd-navbar-nav-hover-color: #FFF;
$rd-navbar-nav-hover-background: $primary;
$rd-navbar-nav-active-color: $rd-navbar-nav-hover-color;
$rd-navbar-nav-active-background: $rd-navbar-nav-hover-background;

//RD Navbar dropdown
$rd-navbar-dropdown-width: 245px;
$rd-navbar-dropdown-background: $white;
$rd-navbar-dropdown-item-color: $brand-silver-chalice;
$rd-navbar-dropdown-item-background: transparent;
$rd-navbar-dropdown-item-hover-color: $primary;
$rd-navbar-dropdown-item-hover-background: transparent;
$rd-navbar-dropdown-item-active-color: $rd-navbar-dropdown-item-hover-color;
$rd-navbar-dropdown-item-active-background: $rd-navbar-dropdown-item-hover-background;

//RD Navbar megamenu
$rd-navbar-megamenu-gutter: 30px;
$rd-navbar-megamenu-background: $white;
$rd-navbar-megamenu-item-color: $brand-silver-chalice;
$rd-navbar-megamenu-item-background: transparent;
$rd-navbar-megamenu-item-hover-color: $primary;
$rd-navbar-megamenu-item-hover-background: transparent;

//RD Navbar togles
$rd-navbar-toggle-preset: "button-lines-arrow-2";
$rd-navbar-search-toggle-preset: "search-preset-1";
$rd-navbar-collapse-toggle-preset: "collapse-preset-1";

//RD Navbar Fixed
$rd-navbar-fixed-height: $rd-navbar-min-height;
$rd-navbar-fixed-line-height: $rd-navbar-min-line-height;
$rd-navbar-fixed-font-size: $rd-navbar-min-font-size;
$rd-navbar-fixed-panel-color:  $black;
$rd-navbar-fixed-toggle-color: $black;
$rd-navbar-fixed-panel-background: $white;
$rd-navbar-fixed-shadow: 0 0 13px -1px rgba(0, 0, 0, 0.17);

//RD Navbar static indents
$navbar-static-nav-indent: 15px;

//RD Navbar fullwidth indents
$navbar-fullwidth-nav-indent: 20px;


//== Dividers
//
//##

//** Hr
$hr-height:                           1px;
$hr-padding:                          0px;
$hr-offset-top:                       30px;
$hr-offset-bottom:                    30px;
//** hr dashed
$hr-dashed-background:                transparent;
$hr-dashed-height:                    0;
$hr-dashed-border-bottom:             1px dashed $gray;
//** Divider
$divider-background:                  $gray;
$divider-width:                       50px;
$divider-height:                      4px;
$divider-padding:                     0;
$divider-offsets:                     29px auto 34px;
//** Divider Vertical
$divider-vertical-background:         $gray;
$divider-vertical-width:              1px;
$divider-vertical-height:             24px;
//** Sizing
$divider-xs-width:                    30px;
$divider-xs-height:                   2px;
$divider-sm-width:                    30px;
$divider-lg-width:                    70px;
$divider-lg-height:                   4px;
//** Text Subline
$text-subline-background:             $primary;
$text-subline-padding:                0;
$text-subline-height:                 1px;
//** before
$text-subline-before-background:      $primary;
$text-subline-before-position-top:    0;
$text-subline-before-position-left:   0;
$text-subline-before-width:           50px;
$text-subline-before-height:          1px;

//** Accordions
$accordion-color:                         $black;
$accordion-color-hover:                   $primary;
$accordion-active-color:                  $accordion-color-hover;
$accordion-list-color:                    $primary;
$accordion-list-active-color:             $accordion-color-hover;

$accordion-arrow-icon-position:           relative;
$accordion-arrow-icon-position-right:     32px;
$accordion-arrow-icon-width:              32px;
$accordion-accordion-inset:               18px 60px 18px 0;
$accordion-list-inset:                    0 0 13px;
$accordion-content-inset:                 25px 30px 30px 30px;

$accordion-size:                          18px;
$accordion-weight:                        900;
$accordion-list-weight:                   $accordion-weight;
$accordion-list-size:                     $accordion-size;
$accordion-arrow-icon-size:               22px;
$accordion-arrow-icon-weight:             700;
$accordion-arrow-icon-family:             "Material Design Icons";
$accordion-arrow-icon:                    '\f238';
$accordion-arrow-icon-active:             '\f236';

$accordion-md-border-bottom:              none;
$accordion-md-border-top:                 none;
$accordion-border:                        1px solid $gray;

//** Common Horizontal and Vertical Tabs
$accordion-tabs-list-border-background:                     $gray-darker;
$accordion-tabs-border-position:                            absolute;
$accordion-tabs-border-position-left:                       50%;
$accordion-tabs-list-border-position-bottom:                0;
$accordion-tabs-list-border-width:                          0;
$accordion-tabs-list-border-active-width:                   100%;
$accordion-tabs-list-border-height:                         1px;
$accordion-tabs-content-inset-top:                          25px;
$accordion-tabs-list-offset-left:                           10px;
$accordion-tabs-list-border-top:                            none;
$accordion-tabs-list-border-bottom:                         none;
$accordion-tabs-boxed-list-letter-color:                    $primary;
$accordion-tabs-boxed-list-inset:                           7px 28px;
$accordion-tabs-boxed-list-size:                            14px;
$accordion-tabs-boxed-list-weight:                          700;
$accordion-tabs-boxed-list-letter-spacing:                  .06em;
//** 992
$accordion-tabs-boxed-list-md-border-bottom:                1px solid $gray;
$accordion-tabs-boxed-list-md-li-border:                    2px solid $primary;
$accordion-tabs-boxed-list-md-offset-top:                   0;
$accordion-tabs-boxed-content-md-border-bottom:             none;
$accordion-tabs-boxed-content-md-border-right:              none;
//** after styling
$accordion-tabs-boxed-list-md-li-after-position:            absolute;
