//
// Posts
// --------------------------------------------------


// Post Box
// --------------------------------------------------

.post-box {
  display: inline-block;
  border-radius: 4px;
  box-shadow: $shadow-area-sm;

  .post-box-img-wrap {
    img {
      width: 100%;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
    }
  }

  .post-box-title {
    line-height: 1.2;
  }

  .post-box-caption {
    padding: 17px 12px 19px 19px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    background: $white;
  }

  * + .list-inline { margin-top: 12px; }
  * + p { margin-top: 12px; }
  * + .button { margin-top: 25px; }

  @include media-breakpoint-up(xl) {
    &, .post-box-title a {
      transition: .3s all ease;
    }

    &:hover {
      box-shadow: $shadow-area-lg;
    }

    .post-box-title a {
      &:hover {
        color: $primary;
      }
    }
  }
}

@include media-breakpoint-down(xs) {
  .post-box {
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
  }
}


// Post Box Wide
// --------------------------------------------------

.post-box.post-box-wide {
  .post-box-img-wrap img {
    max-height: 240px;
  }

  .post-box-caption {
    padding: 29px 25px;
  }

  @include media-breakpoint-up(md) {
    .post-box-caption {
      padding: 31px 60px 40px;
    }
  }
  @include media-breakpoint-up(md) {
    .post-box-img-wrap img {
      max-height: 300px;
    }
  }
  @include media-breakpoint-up(lg) {
    .post-box-img-wrap img {
      max-height: none;
    }
  }
}

.post-box-wide {
  * + .list-inline { margin-top: 4px; }
}


// Post Box Left
// --------------------------------------------------

.post-blog-left {

  @include media-breakpoint-up(md) {
    &.post-box .post-box-caption {
      padding-left: 35px;
      padding-right: 35px;
    }
  }
  @include media-breakpoint-up(xl) {

    &,.post-box-caption {
      display: flex;
    }

    &.post-box .post-box-caption {
      padding-top: 51px;
      padding-left: 60px;
      padding-right: 52px;
    }

    .post-box-img-wrap {
      flex-basis: 31.7%;
    }

    .post-box-title {
      width: 100%;
    }

    .post-box-caption {
      flex-basis: 68.3%;
      align-items: center;
      flex-wrap: wrap;
      padding: 50px 50px 50px 59px;
    }

    &.post-box .post-box-img-wrap img {
      border-top-left-radius: 4px;
      border-top-right-radius: 0;
      border-bottom-left-radius: 4px;
    }

    &.post-box .post-box-caption {
      border-bottom-left-radius: 0;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }

    * + .list-inline { margin-top: 0; }
    * + .button { margin-top: 15px; }
    * + p { margin-top: 4px; }
  }
}


// Post Masonry
// --------------------------------------------------

.post-masonry {
  .post-box-caption {
    padding: 35px 30px 40px;
  }

  * + .list-inline { margin-top: 15px; }
  * + p { margin-top: 15px; }
}


// Post Modern
// --------------------------------------------------

.post-modern {
  position: relative;
  border: 1px solid $white;
  display: block;

  &-title a {
    &:hover {
      text-decoration: underline;
    }
  }

  &-img-wrap {
    position: relative;

    img {
      width: 100%;
    }

    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 70%;
      @include gradient-directional(rgba($black, .9), transparent, 0deg);
    }
  }

  &-caption {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    text-align: center;
    transform: translate(-50%, -50%);
  }

  * + .post-modern-meta { margin-top: 15px; }
  .h3 + .post-modern-meta { margin-top: 25px; }

  @include media-breakpoint-down(sm) {
    .post-modern-title {
      font-size: 18px;
    }

    .post-modern-meta > .list-inline {
      font-size: 10px;

      &.list-inline-dashed-sm {
        margin-left: -15px;

        li {
          padding-left: 15px;

          &:after {
            right: -12.5px;
          }
        }
      }
    }
  }
  @include media-breakpoint-up(md) {
    .post-modern-caption {
      top: auto;
      left: 35px;
      bottom: 55px;
      text-align: left;
      width: auto;
      transform: translate(0, 0);
    }
  }
  @include media-breakpoint-between(xl, xl) {
    .post-modern-title.h3 {
      font-size: 28px;
    }
  }

  @include media-breakpoint-up(xxl) {
    &.post-modern-lg {
      .post-modern-caption {
        left: 110px;
        bottom: 130px;
      }
    }

    .post-modern-caption {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}

// Post Comment Inner
// --------------------------------------------------

@include media-breakpoint-up(xl) {
  .post-comment-inner {
    .post-comment {
      &, .unit-left {
        position: relative;
      }

      &:before, .unit-left:before {
        content: '';
        position: absolute;
        background: $brand-alto;
      }

      &:before {
        top: -4px;
        left: -28px;
        width: 1px;
        height: 152px;
      }

      .unit-left:before {
        top: 15px;
        left: -27px;
        height: 1px;
        width: 6px;
      }

      &:nth-child(2) {
        &:before {
          display: none;
        }
      }
    }
  }
}

.post-comment {
  .text-small + .text-extra-small-10 { margin-top: 0; }
  .text-extra-small-10 + .text-small { margin-top: 12px; }
  + + .button-link { margin-top: 4px; }
  + + .list-inline { margin-top: 0; }
  + + .button { margin-top: 35px; }
}

.post-comment-inner {
  .post-comment + * { margin-top: 12px; }
}

* + .post-comment { margin-top: 30px; }

.blog-aside {

  * + .hr { margin-top: 40px; }
  * + .button { margin-top: 25px; }
  * + .list-modern { margin-top: 20px; }
  .hr + * { margin-top: 35px; }
  p.text-black + * { margin-top: 20px; }
}

.box-range + .hr { margin-top: 60px; }